<template>
  <div class="main">
    <div class="row">
      <div class="container coursePageAll">
        <div class="coursePageTitle">
          <h3>教師專區</h3>
        </div>
        <Header />
        <div class="pageBoxOuter">
          <div class="stepTitle">
            <h4><span>Step1 - </span>建立班級</h4>
          </div>
          <div class="pageBox" v-if="classRoom">
            <div class="processBar x1"></div>
            <div class="colx2 stepInput">
              <div class="formRow">
                <label>班級名稱</label>
                <input
                  type="text"
                  placeholder=""
                  required
                  v-model="classRoom.ClassRoomName"
                />
              </div>
              <div class="formRow">
                <label>課程主題</label>
                <input
                  type="text"
                  placeholder=""
                  required
                  v-model="classRoom.LessonTitle"
                />
              </div>
            </div>
            <div class="colx2 stepInput">
              <div class="formRow">
                <label>課程起始時間</label>
                <input
                  type="date"
                  placeholder="請輸入課程起始時間"
                  required
                  v-model="classRoom.StartTime"
                />
              </div>
              <div class="formRow">
                <label>課程結束時間</label>
                <input
                  type="date"
                  placeholder="請輸入課程結束時間"
                  required
                  v-model="classRoom.EndTime"
                />
              </div>
            </div>
            <div class="stepControl">
              <div class="nextBtn">
                <a @click="next" href="javascript:void(0);">下一步</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Header from "@/components/Teacher/Header.vue";
import ClassRoom from "@/plugins/classroom.js";
import moment from "moment";

export default {
  data() {
    return {
      classRoom: null,
    };
  },
  components: {
    Header,
  },
  async mounted() {
    this.setBase("tSpace page");
    var routeName = this.$route.name;
    if (routeName == "TeacherCreateClass") {
      this.classRoom = await ClassRoom.newInstance();
    } else if (routeName == "TeacherCreateClassPrev") {
      this.classRoom = ClassRoom.getClassRoom();
      if (!this.classRoom) {
        this.classRoom = await ClassRoom.newInstance();
      }
    } else if (routeName == "TeacherEditClass") {
      var response = await this.$api.getClassRoom(this.$route.params.id);
      response.StartTime = moment(response.StartTime).format("YYYY-MM-DD");
      response.EndTime = moment(response.EndTime).format("YYYY-MM-DD");
      ClassRoom.setClassRoom(response);
      this.classRoom = response;
    } else if (routeName == "TeacherCopyClass") {
      var response = await this.$api.getClassRoom(this.$route.params.id);
      response.StartTime = moment(response.StartTime).format("YYYY-MM-DD");
      response.EndTime = moment(response.EndTime).format("YYYY-MM-DD");
      response.ClassRoomID = -1;
      ClassRoom.setClassRoom(response, true);
      this.classRoom = response;
    }
  },
  methods: {
    next() {
      if (!this.validate(".pageBox")) {
        return;
      }
      ClassRoom.save();
      this.$router.push("/teacher/class/create/lessons");
    },
  },
};
</script>